// Aside

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;

.dashboard{
    display: flex;
    height: 100%; 
}


.mobile_dash{
    display: inline;

    .nav_dash{
        background-color: c.$brown-800;
        width: 5rem;
        height: 4rem;
        padding: 0;
        margin: 0;
    }
    @include m.tablet{
        display: none;
    }
}

.contenedor_aside{
    background-color: c.$brown-800;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: space-between;
    height: 150vh; 

    width: 0rem;
    opacity: 0;
    transform: translateX(0rem);
    transition: all .75s ease;
    visibility: hidden;

    @include m.tablet{
        visibility: visible;
        opacity: 1;
        display: flex;
        width: 20rem;
    }
    
        .conte_cabecera{
            text-align: left;

            .cabecera{
                .conte_logo{
                    text-align: center;
                    .logo{
                        width: 10rem;
                    }
                }

                .conteUsuario{
                    text-align: center;
                    img{
                        width: 6rem;
                        height: 6rem;
                        border-radius: 50%;
                    }
                    
                    h4{
                        font-size: 1.1rem;
                    }
                }

                .conte_titulo{
                    text-align: center;
                    margin-top: 2rem;
                    a{
                        color: c.$blanco;
                        font-weight: t.$bold;
                    }
                }
            }

            .sidebar{
                display: block;
                width: auto;
                padding: 0;
                margin: 1rem;


            }
        }

        .cerrar_sesion{

            a{
                text-align: center;
                color: c.$blanco;
                display: block;
                margin-bottom: 0.5rem;
                padding: 0.7rem;
                transition-property: backgroud-color;
                transition-duration:  300ms;


                &:hover{
                    background-color: darken( rgb(202, 148, 241), 20%);
                    border-radius: 0.5rem;
                }
            }
        }
}

.mostrar_contenedor_aside{
    width: 20rem;
    opacity: 1;
    transform: translateX(0rem);
    transition: all .75s ease;
    visibility: hidden;

}

.contenido_dash{
    display: block;
    width: 100%;

    .barraDash{
        position: -webkit-sticky;
        position: sticky;
        top: 0;
        
        background-color: c.$amarillo;
        box-shadow: 1px 12px 9px -13px rgba(0, 0, 0,.4);
        padding: 2rem;
        height: 6rem;

        .relog{
            color: c.$blanco;
            font-size: 1.3rem;
            font-weight: t.$bold;
        }
    }
}

